<template>
  <section class="page">
    <child-comment-list></child-comment-list>
    <p id="bottom"></p>
  </section>
</template>

<script>
  import { ChildCommentList } from '@/modules/Comment'

  export default {
    name: 'PhotoCommentChildcommentIndex',

    components: {
      ChildCommentList
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        // console.log(document.querySelector(to.hash))
        setTimeout(() => {
          document.querySelector(to.hash).scrollIntoView()
          if (vm.$route.query.comment === 'create') document.commentForm.content.focus()
        }, 200)
      })
    }
  }
</script>



